body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Manrope', sans-serif!important;
}

.gallery-image {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.3s;
}

.gallery-image .img {
  display: block;
  cursor: pointer;
  transition: transform 0.3s;
  width: calc(100% - 12px);
  height: calc(100% - 12px);
}

@media (max-width: 768px) {
  .gallery-image .img {
    width: calc(100% - 6px);
    height: calc(100% - 6px);
  }
}

.placeholder {
  background: lightgray;
}

.overlay-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0,0,0,0.9);
  
  display: flex;
  flex-direction: column;
}

.image-overlay {
  --n: 1;
  --i: 0;
  --tx: 0px;
  --ty: 0px;
  display: flex;
  align-items: center;
  width: 100%;
  height: calc(100% - 84px);
  width: calc(var(--n)*100%);
  opacity: calc(1 - var(--y) / 500);
  transform: translate(
    calc(var(--i, 0) / var(--n, 1) * -100% + var(--tx, 0px)), 
    var(--ty, 0px)
  );
}
.image-overlay.use-animation {
  transition: transform .5s ease-out;
}
.image-overlay .slide {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%; 
  width: calc(100%/var(--n));
  height: 100%;
  max-height: 100%;
}
.image-overlay img {
  display: block;
  max-width: calc(100% - 40px);
  max-height: calc(100% - 40px);
  margin: auto;
  pointer-events: none;
}

.overlay-wrapper header {
  color: white;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: space-between;
  padding: 30px;
}
.overlay-wrapper header span {
  text-align: center;
}
.overlay-wrapper header a {
  color: white;
}
.overlay-wrapper header a:hover {
  color: white;
}
.overlay-wrapper header button {
  font-size: 24px;
  line-height: 24px;
  background: none!important;
  border: none;
  padding: 0!important;
  color: white;
  cursor: pointer;
  text-align: end;
}


.image-overlay section {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100% - 60px);
}

.image-overlay header span {
  font-size: 18px;
  display: inline-block;
  margin: 12px auto;
  color: #efefef;
  width: 33.333333%;
  text-align: center;
}

.image-overlay header span:first-child {
  text-align: left;
}

.image-overlay header span:last-child {
  text-align: right;
  background-image: url("./assets/multiply.svg");
  background-repeat: no-repeat;
  background-position: right center;
  background-size: 12px;
  cursor: pointer;
}

.image-overlay header span a {
  margin: 0 auto;
  color: #f5f5f5;
  text-decoration: none;
}

.image-overlay header span a:hover {
  color: #f1f1f1;
}


.overlay-wrapper .arrow {
  position: absolute;
  top: 80px;
  bottom: 80px;
  width: 50px;
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
  background-position: center;
  background-size: 30px;
  background-repeat: no-repeat;
}

@media (max-width: 425px) {
  .overlay-wrapper .arrow {
    display: none;
  }
}

.overlay-wrapper .arrow:hover {
  background-color: rgba(0, 0, 0, 0.3);
}

.overlay-wrapper .arrow-left {
  left: 0;
  background-image: url("./assets/back.svg");
}

.overlay-wrapper .arrow-right {
  right: 0;
  background-image: url("./assets/next.svg");
}

.project {
  width: calc(100% - 48px);
  margin: 0 auto;
  min-height: calc(100vh - 56px);
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  column-gap: 24px;
}

.project .img-wrapper {
  max-width: 100%;
  max-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 600px;
}

.project .img-wrapper img {
  margin-top: 40px;
}


.project .img-wrapper > * {
  display:block;
  max-width: 100%;
  max-height: 100%;
}

.project .img-wrapper .text {
  max-width: 80%;
  margin-left: 0;
  margin-right: auto;
}

.mobile-title {
  display: none!important;
}

.project p {
  font-size: 15px;
  width: 80%;
}
.project h1 {
  font-weight: 800;
}

.project button {
  margin-top: 12px;
  border-radius: 0;
  border: 1px solid black;
  color: black;
  transition: all .2s;
  font-size: 15px;
  line-height: 24px;
  padding-top: 3px;
  padding-bottom: 3px;
}

.project button:hover {
  border-radius: 0;
  border: 1px solid black;
  color: white;
  background-color: black;
}

.desktop-title {
  font-size: 54px;
  line-height: 58px;
  margin-top: 58px;
  margin-bottom: 58px;
}


@media (max-width: 425px) {
  .project {
    grid-template-columns: 1fr;
    width: calc(100% - 24px);
    min-height: 0;
    height: auto;
  }
  .project .img-wrapper {
    min-height: 0;
    height: auto;
  }
  .project .img-wrapper > * {
    margin: 24px 0;
    max-width: 100%;
    max-height: 100%;
  }
  .mobile-title {
    display: block!important;
    margin: 0;
  }
  .desktop-title {
    display: none!important;
  }
  .project p {
    font-size: 15px;
    width: 100%;
  }
}

.dropdown-item:active {
  background-color: #f8f9fa!important;
  color: black!important;
}

